#overlay {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#content {
  width: 340px;
  z-index: 2;
  display: grid;
  justify-content: center;
  align-items: center;
}
.game-buttons {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 320px;
}
.next-game-button {
  width: 170px;
  height: 50px;
  margin: 2% auto;
  text-align: center;
  color: aliceblue;
  border: 2px solid transparent;
  border-color: aliceblue;
  background-color: #282c34;
  font-size: 1.5rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  padding: 0;
}
#modal-accuracy {
  font-size: 1.2rem;
  color: aliceblue;
  text-align: center;
  margin-bottom: 20px;
}
#modal-accuracy tbody {
  font-size: 2.5rem;
  color: rgb(234, 0, 32);
}

.accuracy-thead {
  width: 320px;
}
.accuracy-thead tr th {
  padding: 0 10px;
  width: 50%;
}