header {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  height: 50px;
  border-bottom: 1px solid #d3d6da;
}

#App {
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.menu-left,
.menu-right {
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
  width: 20%;
  justify-content: flex-start;
}

#nav-button {
  padding-top: 2px;
}

button.icon {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0 4px;
}

#range-button {
  margin-left: auto;
  margin-right: 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.table th {
  vertical-align: middle;
  color: aliceblue;
}

#help-button {
  margin-left: 46px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.title {
  overflow: hidden;
  display: flex;
  align-items: center;
  font-family: impact;
}
.empty-text {
  width: 22px;
  height: 38px;
  display: inline-block;
  position: relative;
}
.title span {
  display: inline-block;
  color: aliceblue;
  font-weight: 700;
  font-size: 37px;
  line-height: 100%;
  letter-spacing: 0.01em;
}
.title:hover span {
  transition: 2000ms;
  -webkit-transform: rotateX(360deg);
  transform: rotateX(360deg);
}
.title .suit {
  position: absolute;
  top: -100%;
  left: -1px;
}
.title .character-o {
  position: absolute;
  top: 1px;
  left: 1px;
}
.title:hover .suit {
  top: 1px;
  color: rgb(234, 0, 32);
  -webkit-transition-delay: 800ms;
  transition-delay: 800ms;
}
.title:hover .character-o {
  -webkit-transition-delay: 800ms;
  transition-delay: 800ms;
  top: 100%;
}
.title:hover .character-f {
  -webkit-transition-delay: 10ms;
  transition-delay: 10ms;
}
.title:hover .character-l {
  -webkit-transition-delay: 400ms;
  transition-delay: 400ms;
}
.title:hover span:nth-child(4) {
  -webkit-transition-delay: 1200ms;
  transition-delay: 1200ms;
}
.title:hover span:nth-child(5) {
  -webkit-transition-delay: 1600ms;
  transition-delay: 1600ms;
}
.title:hover span:nth-child(6) {
  -webkit-transition-delay: 2000ms;
  transition-delay: 2000ms;
}
.ReactModal__Content {
  min-width: 320px;
}
