#game {
  height: 100%;
  display: grid;
  flex-direction: column;
}

#game-container,
#board-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

#hand {
  min-width: 320px;
  box-sizing: border-box;
}

#board {
  width: 320px;
  box-sizing: border-box;
  position: relative;
}

#result-none {
  position: absolute;
  bottom: 5%;
  right: 5%;
  font-size: 1.4rem;
  color: aliceblue;
}

#actions {
  width: 100%;
  box-sizing: border-box;
}

#action-button-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
#game-accuracy {
  height: 100%;
  width: 49%;
  font-size: 0.8rem;
  display: grid;
}
#game-accuracy tbody {
  font-size: 1rem;
}
